<template>
  <div class="login">
    <div class="login-block">
      <el-tabs type="card" v-model="curTab">
        <el-tab-pane label="登录" name="login">
          <el-form ref="form" :model="loginForm" label-width="80px">
            <el-form-item label="用户名" prop="username">
              <el-input v-model="loginForm.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onLogin">登录</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="注册" name="register">
          <el-form ref="form" :model="registerForm" label-width="80px">
            <el-form-item label="用户名" prop="username">
              <el-input v-model="registerForm.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="registerForm.password"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="rePassword">
              <el-input type="password" v-model="registerForm.rePassword"></el-input>
            </el-form-item>
            <el-form-item label="手机" prop="phone">
              <el-input v-model="registerForm.phone"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onRegister">注册</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>

    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      curTab: 'login',
      loginForm: {
        username: '',
        password: ''
      },
      registerForm: {
        username: '',
        password: '',
        rePassword: '',
        phone: ''
      }
    }
  },
  methods: {
    onLogin () {
      console.log('login!')
      const config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      const formData = new FormData()
      formData.append('username', this.loginForm.username)
      formData.append('password', this.loginForm.password)

      this.$http.post('/login', formData, config).then(res => {
        const json = res.data
        if (json.code === 200) {
          window.sessionStorage.setItem('token', json.token)
          window.sessionStorage.setItem('username', json.data)
          this.$emit('update-timer')
          this.$message.success('登陆成功')
          this.$router.push('/')
        } else {
          this.$message.error('登陆失败')
        }
      })
    },
    onRegister () {
      console.log('register!')
      this.$http.post('/register', this.registerForm).then(res => {
        const json = res.data
        if (json.code === 200) {
          this.$message.success('注册成功')
          this.$router.go(0)
        } else {
          this.$message.error('注册失败')
        }
      })
    }
  }
}
</script>
<style scoped>
.login {
  width: 960px;
  margin: 0 auto;
}
.login-block {
  width: 480px;
  margin: 0 auto;
  margin-top: 100px;
  padding: 40px;
  background-color: #d7ffdc;
  border-radius: 10px;
}
</style>
